










































































































































































import { Vue, Component } from 'vue-property-decorator';

@Component
export default class AuthLayout extends Vue {
  public appDrawerState = true;
  public sideNavigationLinks = [
    {
      icon: 'mdi-chart-line',
      title: 'Dashboard',
      to: '/dashboard'
    },
    {
      icon: 'mdi-cash-multiple',
      title: 'Tax Payer',
      to: '/d/taxpayer'
    },
    {
      icon: 'mdi-clipboard-outline',
      title: 'Tax Collector',
      sub: [
        {
          icon: '',
          title: 'Validation',
          to: '/d/taxcollector/validation'
        },
        {
          icon: '',
          title: 'Reports',
          to: '/d/taxcollector/reports'
        }
      ]
    },
    {
      icon: 'mdi-barcode',
      title: 'Tax Liquidator',
      to: '/d/taxliquidator'
    },
    {
      icon: 'mdi-chart-pie',
      title: 'Cashiering',
      to: '/d/cashier'
    },
    {
      icon: 'mdi-server',
      title: 'Settings',
      sub: [
        {
          icon: '',
          title: 'Logs',
          to: '/settings/logs'
        },
        {
          icon: '',
          title: 'ACL',
          to: '/settings/acl'
        },
        {
          icon: '',
          title: 'Calculation',
          to: '/settings/calculation'
        },
      ]
    }
  ];

  public onAppDrawerToggle(): void {
    this.appDrawerState = !this.appDrawerState;
  }

  public async onSignOut() {
    this.$store.commit('toggleProgressBarState');
    try {
      const { data: { message } } = await this.$axios.put('/auth/logout');

      localStorage.removeItem('user'); 
      this.$router.replace('/auth/signin');

      this.$store.commit('toggleSnackBarState', {
        message,
        color: 'success',
        toggle: true
      });

    } catch(error) {
      console.log(error);
      this.$store.commit('toggleSnackBarState', {
        message: error.message,
        color: 'red accent-2',
        toggle: true
      });
    }

    this.$store.commit('toggleProgressBarState');
  }
}
